.header {
  font-weight: 500;
  font-size: 12px;
  font-style: normal;
  display: flex;
  padding: 0 50px;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0.01px 2px 4px rgba(0, 0, 0, 0.05);

  &__logo {
    display: block;
    width: 40px;
    height: 40px;
  }

  &__list {
    display: flex;
  }

  &__list-item {
    display: flex;
    line-height: 60px;
    text-transform: uppercase;
    position: relative;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__link {
    text-decoration: none;
    color: $main-dark;

    &:hover {
      color: $main-blue;
    }
  }
}

.is-active {
  color: $main-blue;
  text-decoration: none;
}

.is-active::after {
  display: block;
  background-color: $main-blue;
  position: absolute;
  width: 100%;
  height: 4px;
  bottom: 0;
  border-radius: 8px;
  content: "";
}
