.card {
  width: $cardWidth;
  height: $cardHeight;
  border: 1px solid $light-border;
  border-radius: 8px;
  padding: $cardWidth * 0.08;
  padding-top: $cardWidth * 0.16;
  margin-bottom: $cardWidth * 0.23;

  &:not(:nth-child(4n)) {
    margin-right: $cardWidth * 0.24;
  }

  &:nth-last-child(-n + 4) {
    margin-bottom: 0;
  }

  &__img {
    display: block;
    margin: auto;
    margin-bottom: 40px;
    width: 160px;
    height: 134px;
  }

  &__product-name {
    margin-bottom: 4px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $main-dark;
  }

  &__product-code {
    margin-bottom: 16px;
    margin-top: 0;
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: $secondary;
  }

  &__stars {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    font-size: 10px;
    line-height: 16px;
    font-weight: 400;
    color: $main-dark;

    &-block {
      display: flex;
    }

    &-star {
      background-image: url(../images/star.svg);
      background-repeat: no-repeat;
      background-position: center;
      width: 16px;
      height: 16px;
      margin-right: 4px;
    }

    &-block &-star:nth-child(-n+4) {
      background-image: url(../images/star-active.svg);
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    &-price {
      margin: 0;
      font-size: 12px;
      line-height: 18px;
      font-weight: normal;
      color: $secondary;
    }
    
    &-value {
      margin: 0;
      font-size: 16px;
      line-height: 18px;
      font-weight: bold;
      color: $main-dark;
    }
  }

  &__button {
    display: flex;
    background-color: $main-blue;
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    width: 166px;
    height: 40px;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
  }
  
  &__button:hover {
    border: 1px solid $main-blue;
    background-color: white;
    color: $main-blue;
  }
}
